import { FormErrors } from '../errors/validation.error';
import { TicketForm } from '../forms/ticket.forms';
import { createValidator, ValidationRules } from '../utils/validation.utils';

export const validateTicketForm = (form: TicketForm): FormErrors<TicketForm> => {
    const errors: FormErrors<TicketForm> = {};

    errors.equipmentDescription = createValidator([
        { check: (v) => ValidationRules.required(v), message: 'Equipment description is required' },
        { check: (v) => ValidationRules.minLength(v, 5), message: 'Equipment description must be at least 5 characters' },
        { check: (v) => ValidationRules.maxLength(v, 500), message: 'Equipment description must be less than 500 characters' }
    ])(form.equipmentDescription);

    errors.imageUrl = createValidator([
        { check: (v) => !v || ValidationRules.url(v), message: 'Please enter a valid URL' }
    ])(form.imageUrl);

    errors.client = createValidator([
        { check: (v) => v && typeof v.id === 'number' && v.id > 0, message: 'Please select a client' }
    ])(form.client);

    errors.estimatedCost = createValidator([
        { check: (v) => v >= 0, message: 'Estimated cost cannot be negative' }
    ])(form.estimatedCost);

    errors.estimatedDeliveryDate = createValidator([
        {
            check: (v) => !v || ValidationRules.date(v),
            message: (v) => `Please enter a valid date. Current value: "${v}"`
        },
    ])(form.estimatedDeliveryDate);

    errors.estimatedExecutionTime = createValidator([
        { check: (v) => v >= 0, message: 'Estimated execution time cannot be negative' }
    ])(form.estimatedExecutionTime);

    errors.observations = createValidator([
        { check: (v) => !v || ValidationRules.maxLength(v, 1000), message: 'Observations must be less than 1000 characters' }
    ])(form.observations);

    errors.priority = null;
    errors.status = null;

    errors.representative = null;
    errors.employees = null;

    return errors;
};
